import Vue from 'vue';
import Vuex from 'vuex';
import * as Constant from '../scripts/constants';
import Enumeration from '../scripts/enumeration'

Vue.use(Vuex)

const storeData={
    state:{
        toggleNavbar:false,
        toggleMenu:false,
        formMode:Enumeration.FormMode.Add,
        isShowEmployeeDialog:false,
        isShowCBDialog:false,
        teachers:[],
        entity:null,
        combinationSubject:null,
        messageWarning:"",
        messageDelete:"",
        teacherFilter:"",
        combinationSubjectId:"",
        listTeacherId:[]
    },
    getters:{

        [Constant.GET_TEACHERS]:state=>state.teachers,
        [Constant.GET_FORMMODE]:state=>state.formMode,
        [Constant.GET_TOGGLENAVBAR]:state=>state.toggleNavbar,
        [Constant.GET_TOGGLEMENU]:state=>state.toggleMenu,
        [Constant.GET_SHOWCBDIALOG]:state=>state.isShowCBDialog,
        [Constant.GET_SHOWEMPLOYEEDIALOG]:state=>state.isShowEmployeeDialog,
        [Constant.GET_ENTITY]:state=>state.entity,
        [Constant.GET_COMBINATIONSUBJECT]:state=>state.combinationSubject,
        [Constant.GET_MESSAGEWARNING]:state=>state.messageWarning,
        [Constant.GET_MESSAGEDELETE]:state=>state.messageDelete,
        [Constant.GET_TEACHERFILTER]:state=>state.teacherFilter,
        [Constant.GET_COMBINATIONSUBJECTID]:state=>state.combinationSubjectId,
        [Constant.GET_LISTTEACHERID]:state=>state.listTeacherId,
    },
    mutations:{


        [Constant.SET_TEACHERS]:(state,value)=>(state.teachers=[...value]),
        [Constant.SET_FORMMODE]:(state,value)=>(state.formMode=value),
        [Constant.SET_TOGGLENAVBAR]:(state)=>(state.toggleNavbar=!state.toggleNavbar),
        [Constant.SET_TOGGLEMENU]:(state)=>(state.toggleMenu=!state.toggleMenu),
        [Constant.SET_SHOWCBDIALOG]:(state,value)=>(state.isShowCBDialog=value),
        [Constant.SET_SHOWEMPLOYEEDIALOG]:(state,value)=>(state.isShowEmployeeDialog=value),
        [Constant.SET_ENTITY]:(state,value)=>(state.entity=value),
        [Constant.SET_COMBINATIONSUBJECT]:(state,value)=>(state.combinationSubject=value),
        [Constant.SET_MESSAGEWARNING]:(state,value)=>(state.messageWarning=value),
        [Constant.SET_MESSAGEDELETE]:(state,value)=>(state.messageDelete=value),
        [Constant.SET_TEACHERFILTER]:(state,value)=>(state.teacherFilter=value),
        [Constant.SET_COMBINATIONSUBJECTID]:(state,value)=>(state.combinationSubjectId=value),
        [Constant.SET_LISTTEACHERID]:(state,value)=>(state.listTeacherId=[...value]),
    }
    
}
const store=new Vuex.Store(storeData)
export default store