import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const routes = [
  {
    path:'/admin',
    name:'Admin',
    component:()=>import('../components/admin/layouts'),
    redirect:'/',
    children:[
      {
        path:'/',
        name:'Dashboard',
        component:()=>import('../components/admin/views')
        
      },
    ]
  },
  // {
  //   path: '/',
  //   name: 'Employee',
  //   component: () => import('../components/views/dashboard')
  //  }
  // {
  //   path: '/equipment/em',
  //   name: 'Employeek',
  //   component: () => import('../components/views/Index.vue')
  //  },
 
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

export default router
